import React, { useState } from "react";
import { motion } from "framer-motion";
import ImgInscryptionCover from "../images/Inscryption_Cover_Art.jpg";
import ImgInscryptionGameplay from "../images/Inscryption-Gameplay-1024x576.jpg";
import ImgXcomCover from "../images/XCOM_2_cover_art.jpg";
import ImgXcomGameplay from "../images/xcomGameplay.jpg";
import ImgBaldursCover from "../images/baldursgatesCover.jpg";
import ImgBaldursGameplay from "../images/baldursGameplay.jpg";
import ImgJamuno from "../images/jam0.jpg";
import ImgJamdos from "../images/jam0.5.jpg";
import ImgJamtres from "../images/jam1.jpg";
import ImgJamcuatro from "../images/jam11.jpg";
import ImgCrossfit from "../images/crossfit.jpg";
import ImgCrossfitdos from "../images/crossfit1.jpg";
import ImgCrossfittres from "../images/crossfit2.jpg";
import ImgCrossfitcuatro from "../images/crossfit4.jpg";
import ImgAjedrez from "../images/trofeosajedrez.jpg";


const Aficiones = () => {
  // Datos de aficiones
  const aficionesList = [
    {
      id: 1,
      titulo: "JAMS",
      descripcion:
        "Uno de mis pasatiempos favoritos es participar en jams, ya sean Game jams o Jams de programacin. Para las personas que no saben en que consiste una jam, es un concurso donde se da un tema al azar y en un plazo de 48 horas se debe plantear un proyecto que encaje con el tema elegido. Me encanta la emocin de desarrollar ideas creativas en plazos cortos, explorar nuevas tecnologas, y colaborar con otros entusiastas. Es una gran oportunidad para mejorar mis habilidades y compartir con una comunidad apasionada por la creacin y el desarrollo.",
      galeria: [
        {
          id: 1,
          type: "image",
          src: ImgJamuno,
          thumbnail: ImgJamuno,
          description: "Jam uno",
        },
        {
          id: 2,
          type: "image",
          src: ImgJamdos,
          thumbnail: ImgJamdos,
          description: "Jam dos",
        },
        {
          id: 3,
          type: "image",
          src: ImgJamtres,
          thumbnail: ImgJamtres,
          description: "Jam 3",
        },
        {
          id: 4,
          type: "image",
          src: ImgJamcuatro,
          thumbnail: ImgJamcuatro,
          description: "Jam cuatro",
        },
      ],
    },
    {
      id: 2,
      titulo: "Videojuegos",
      descripcion:
        "Los videojuegos son una de mis mayores pasiones, y disfruto explorando una amplia variedad de gneros. Me fascinan especialmente los juegos indie, que destacan por su innovacin y originalidad al tomar riesgos en sus mecnicas y narrativa. Un claro ejemplo es Inscryption, uno de mis juegos favoritos, que desafa al jugador a reaprender sus sistemas a lo largo del gameplay, manteniendo una experiencia dinmica y sorprendente. Tambin disfruto de los juegos que combinan estrategia y gestin, como XCOM y Baldurs Gate 3. Estos ttulos exigen tanto habilidades tcticas como planificacin estratgica, lo cual encuentro enormemente gratificante. En definitiva, los videojuegos no solo son un pasatiempo para m, sino una fuente de inspiracin constante en creatividad e innovacin.",
      galeria: [
        {
          id: 1,
          type: "image",
          src: ImgInscryptionCover,
          thumbnail: ImgInscryptionCover,
          description: "Inscryption portada",
        },
        {
          id: 2,
          type: "image",
          src: ImgInscryptionGameplay,
          thumbnail: ImgInscryptionGameplay,
          description: "Inscryption gameplay",
        },
        {
          id: 3,
          type: "image",
          src: ImgXcomCover,
          thumbnail: ImgXcomCover,
          description: "XCOM portada",
        },
        {
          id: 4,
          type: "image",
          src: ImgXcomGameplay,
          thumbnail: ImgXcomGameplay,
          description: "XCOM gameplay",
        },
        {
          id: 5,
          type: "image",
          src: ImgBaldursCover,
          thumbnail: ImgBaldursCover,
          description: "Baldurs Gate 3 portada",
        },
        {
          id: 6,
          type: "image",
          src: ImgBaldursGameplay,
          thumbnail: ImgBaldursGameplay,
          description: "Baldurs Gate 3 gameplay",
        },
      ],
    },
    {
      id: 3,
      titulo: "Crossfit",
      descripcion:
        "El CrossFit es una de mis pasiones ms grandes. Creo firmemente que mantener un estilo de vida saludable es fundamental para ser activo tanto fsica como mentalmente. Practico CrossFit diariamente, y esta disciplina no solo me ha permitido mejorar mi condicin fsica, sino que tambin me ha enseado la importancia de la disciplina, la constancia y el trabajo en equipo. He participado en algunas competiciones, donde he podido poner a prueba mis habilidades y superar mis lmites. Para m, el CrossFit es ms que un ejercicio; es una comunidad que me motiva a dar lo mejor de m cada da.",
      galeria: [
        {
          id: 1,
          type: "image",
          src: ImgCrossfit,
          thumbnail: ImgCrossfit,
          description: "Crossfit uno",
        },
        {
          id: 2,
          type: "image",
          src: ImgCrossfitdos,
          thumbnail: ImgCrossfitdos,
          description: "crossfit tres",
        },
        {
          id: 3,
          type: "image",
          src: ImgCrossfittres,
          thumbnail: ImgCrossfittres,
          description: "crossfit tres",
        },
        {
          id: 4,
          type: "image",
          src: ImgCrossfitcuatro,
          thumbnail: ImgCrossfitcuatro,
          description: "Crossfit cuatro",
        },
      ],
    },
    {
      id: 4,
      titulo: "Ajedrez",
      descripcion:
        "Desde que era pequeo, el ajedrez ha sido una parte esencial de mi vida. Este juego no solo me fascina, sino que tambin me ayud a enamorarme de la programacin. Recuerdo que en mi primera clase de programacin, me frustr mucho al enfrentarme a un problema complicado, pero, al igual que en el ajedrez, logr resolverlo despus de un tiempo. Para m, cada desafo en la programacin se asemeja a una partida de ajedrez; todos los problemas se pueden visualizar en un tablero cuadriculado y, al igual que en el juego, no puedes culpar a otros por tus errores. Cada movimiento cuenta y cada decisin es crucial, lo que me ha enseado a ser perseverante y analtico en todas las reas de mi vida.",
      galeria: [
        {
          id: 1,
          type: "image",
          src: ImgAjedrez,
          thumbnail: ImgAjedrez,
          description: "Ajedrez trofeos",
        },
        
      ],
    },
  ];

  // Estado para el media seleccionado por aficin
  const [selectedMedia, setSelectedMedia] = useState({
    1: aficionesList[0].galeria[0], // Inicializa la imagen seleccionada para JAMS
    2: aficionesList[1].galeria[0], // Inicializa la imagen seleccionada para Videojuegos
    3: aficionesList[2].galeria[0], // Inicializa la imagen seleccionada para Crossfit
    4: aficionesList[3].galeria[0], // Inicializa la imagen seleccionada para Ajedrez
  });

  const handleMediaSelect = (aficionId, media) => {
    setSelectedMedia((prev) => ({
      ...prev,
      [aficionId]: media, // Actualiza solo la media de la aficin correspondiente
    }));
  };

  return (
    <div className="max-w-7xl mx-auto px-2 mt-4">
      {/* Portada */}
      <motion.div
        className="bg-gray-800 text-white p-6 rounded-lg mb-6 text-center"
        initial={{ opacity: 0, y: -30 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
      >
        <h2 className="text-3xl font-bold mb-2">Mis Aficiones</h2>
        <p className="text-gray-300">
          Porque se puede descubrir mucho de una persona a travs de sus
          aficiones.
        </p>
      </motion.div>

      {/* Lista de Aficiones */}
      <div className="space-y-12 divide-y divide-gray-300">
        {aficionesList.map((aficion) => (
          <motion.div
            key={aficion.id}
            className="grid grid-cols-1 lg:grid-cols-12 gap-8 pt-6"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
          >
            {/* Ttulo */}
            <div className="lg:col-span-2">
              <h3 className="text-2xl font-bold text-gray-800">
                {aficion.titulo}
              </h3>
            </div>

            {/* Descripcin */}
            <div className="lg:col-span-6">
              <p className="text-gray-600 leading-relaxed text-justify">
                {aficion.descripcion}
              </p>
            </div>

            {/* Galera */}
            <div className="lg:col-span-4">
              {/* Media Principal */}
              <div className="bg-gray-100 rounded-lg overflow-hidden mb-4">
                {selectedMedia[aficion.id].type === "video" ? (
                  <video
                    src={selectedMedia[aficion.id].src}
                    controls
                    className="w-full h-64 object-cover"
                  />
                ) : (
                  <img
                    src={selectedMedia[aficion.id].src}
                    alt={selectedMedia[aficion.id].description}
                    className="w-full h-64 object-cover"
                  />
                )}
              </div>

              {/* Miniaturas */}
              <div className="grid grid-cols-4 gap-2">
                {aficion.galeria.map((media) => (
                  <button
                    key={media.id}
                    onClick={() => handleMediaSelect(aficion.id, media)}
                    className={`relative rounded-lg overflow-hidden transition-all duration-200 ${
                      selectedMedia[aficion.id].id === media.id
                        ? "ring-2 ring-blue-500"
                        : "hover:ring-2 hover:ring-gray-300"
                    }`}
                  >
                    <img
                      src={media.thumbnail}
                      alt={media.description}
                      className="w-full h-20 object-cover"
                    />
                  </button>
                ))}
              </div>
            </div>
          </motion.div>
        ))}
      </div>
    </div>
  );
};

export default Aficiones;
