import { Outlet, Link } from "react-router-dom";
import { useState } from "react";

const LayoutNavBar = () => {
  // Estado para controlar el men
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  // Funcin para alternar el men
  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <>
      <nav className="fixed top-0 left-0 w-full bg-gradient-to-r from-slate-900 to-slate-800 shadow-lg z-50">
        <div className="max-w-6xl mx-auto px-4">
          <div className="flex justify-between items-center h-16">
            {/* Logo/Brand */}
            <div className="flex items-center">
              <Link to="/home" className="text-white text-0.2xl font-bold">
                Porfolio Github
              </Link>
            </div>
            {/* Desktop Navigation */}
            <div className="hidden md:flex items-center space-x-8">
              <Link
                to="/home"
                className="text-gray-200 hover:text-white hover:bg-purple-700 px-3 py-2 rounded-md transition-all duration-300 ease-in-out"
              >
                Inicio
              </Link>
              <Link
                to="/proyectos"
                className="text-gray-200 hover:text-white hover:bg-purple-700 px-3 py-2 rounded-md transition-all duration-300 ease-in-out"
              >
                Proyectos
              </Link>
              <Link
                to="/aficiones"
                className="text-gray-200 hover:text-white hover:bg-purple-700 px-3 py-2 rounded-md transition-all duration-300 ease-in-out"
              >
                Aficiones
              </Link>
              <Link
                to="/contacto"
                className="text-gray-200 hover:text-white hover:bg-purple-700 px-3 py-2 rounded-md transition-all duration-300 ease-in-out"
              >
                Contacto
              </Link>
            </div>
            {/* Mobile Navigation Button */}
            <div className="md:hidden flex items-center">
              <button
                onClick={toggleMenu}
                className="p-2 rounded-md hover:bg-purple-700 focus:outline-none focus:ring-2 focus:ring-white"
              >
                <svg
                  className="h-6 w-6 text-white"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M4 6h16M4 12h16M4 18h16"
                  />
                </svg>
              </button>
            </div>
          </div>
          {/* Mobile Menu */}
          {isMenuOpen && (
            <div className="md:hidden">
              <div className="px-2 pt-2 pb-3 space-y-1 sm:px-3">
                <Link
                  to="/home"
                  onClick={toggleMenu}
                  className="block text-gray-200 hover:text-white hover:bg-purple-700 px-3 py-2 rounded-md transition-all duration-300 ease-in-out"
                >
                  Inicio
                </Link>
                <Link
                  to="/proyectos"
                  onClick={toggleMenu}
                  className="block text-gray-200 hover:text-white hover:bg-purple-700 px-3 py-2 rounded-md transition-all duration-300 ease-in-out"
                >
                  Proyectos
                </Link>
                <Link
                  to="/aficiones"
                  onClick={toggleMenu}
                  className="block text-gray-200 hover:text-white hover:bg-purple-700 px-3 py-2 rounded-md transition-all duration-300 ease-in-out"
                >
                  Aficiones
                </Link>
                <Link
                  to="/contact"
                  onClick={toggleMenu}
                  className="block text-gray-200 hover:text-white hover:bg-purple-700 px-3 py-2 rounded-md transition-all duration-300 ease-in-out"
                >
                  Contacto
                </Link>
              </div>
            </div>
          )}
        </div>
      </nav>
      <Outlet />
    </>
  );
};

export default LayoutNavBar;
