import { Outlet, Link } from "react-router-dom";

const LayoutFooter = () => {
  return (
    <>
      <nav className="bottom-0 left-0 w-full bg-gradient-to-r from-slate-900 to-slate-800 shadow-lg z-50">
        <div className="max-w-6xl mx-auto px-4">
          <div className="flex justify-between items-center h-16">
            {/* Columna Izquierda */}
            <div className="text-white">
              <p>Pgina creada en React, tailwind y Framer motion por Iksvaku </p>
            </div>

            {/* Columna Derecha */}
            <div>
              <Link to="/contact" className="text-gray-200 hover:text-white hover:bg-purple-700 px-3 py-2 rounded-md transition-all duration-300 ease-in-out">
                Contacto
              </Link>
            </div>
          </div>
        </div>
      </nav>
      <Outlet />
    </>
  );
};

export default LayoutFooter;
