import React from "react";
import { motion } from "framer-motion";
import VideoMarbac from "../videos/marbac.mp4";
import VideoVTTTRAINING from "../videos/vttraining.mp4";
import VideoMyLife from "../videos/projectLife.mp4";
import VideoBlastheroes from "../videos/blastHeroes.mp4";
import VideoInterviewDualist from "../videos/interviewDualist.mp4";

// Definimos los colores por tecnologa
const colors = {
  WordPress: "bg-blue-500", // Color azul similar al de WordPress
  PlayCanvas: "bg-orange-400", // Color naranja similar al de PlayCanvas
  Unity: "bg-slate-950	", // Color verde similar al de Unity
};

const proyectosData = {
  WordPress: [
    {
      titulo: "VT-TRAINING",
      descripcion: (
        <>
          Desarroll la web profesional VT-TRAINING, una web desarrollada en
          WordPress enfocada al HYROX, de hecho son subcampeones del mundo del
          HYROX sub 29. Implement interfaz, instalacin de wordpress en
          servidor, instalacin del certificado SSL, instalacin de plugins y
          asesoramiento al cliente. La pgina se destaca por su diseo
          responsivo y su interfaz amigable, que facilita el aprendizaje y la
          interaccin en cualquier dispositivo. Puedes acceder en{" "}
          <a
            href="https://vttraining.es/"
            target="_blank"
            rel="noopener noreferrer"
            className="text-blue-400 hover:text-blue-600 font-semibold underline"
          >
            VT-TRAINING
          </a>
          .
        </>
      ),
      videoUrl: VideoVTTTRAINING,
    },
    {
      titulo: "Marbac",
      descripcion: (
        <>
          Desarroll la web profesional Marbac, una web desarrollada en
          WordPress enfocada a las reformas de casas en Madrid, interiorismo y
          diseo. Implement interfaz, instalacin de wordpress en servidor,
          instalacin del certificado SSL, instalacin de plugins y
          asesoramiento al cliente. La pgina se destaca por su diseo
          responsivo y su interfaz amigable, que facilita el aprendizaje y la
          interaccin en cualquier dispositivo. Puedes acceder en{" "}
          <a
            href="https://www.marbac.es/"
            target="_blank"
            rel="noopener noreferrer"
            className="text-blue-400 hover:text-blue-600 font-semibold underline"
          >
            Marbac
          </a>
          .
        </>
      ),
      videoUrl: VideoMarbac,
    },
  ],
  PlayCanvas: [
    {
      titulo: "Blast Heroes",
      descripcion: (
        <>
          Proyecto desarrollado dentro del entorno de Snatchap. Particip en el
          proyecto en una fase avanzada, enfocndome en la integracin de UI y
          modelos 2D/3D en el entorno de juego. Colabor estrechamente con
          programacin y arte para asegurar una experiencia fluida, y gestion
          parches y actualizaciones. integracin de llamadas a APIs y servicios
          de Snatchap para su integracin dentro de la UI. Descripcin del
          producto: Blast Heroes es un juego multijugador estilo Archero en el
          que los jugadores deberan enfrentarse contra hordas de objetos
          embrujados y mejorar sus personajes en cada partida. Es un juego
          roguelike con cada partida consigues mejoras y mejores armas que
          puedes adquirir en la tienda.
          <br />
        </>
      ),
      videoUrl: VideoBlastheroes,
    },
    {
      titulo: "My Life",
      descripcion: (
        <>
          Proyecto desarrollado dentro del entorno de Snatchap. Particip en el
          proyecto en una fase inicial, enfocndome en la integracin de UI y
          modelos 2D/3D en el entorno de juego. Colabor estrechamente con los
          equipos de programacin, arte y diseadores para asegurar una
          experiencia fluida. Integracin de llamadas a APIs y servicios de
          Snatchap para su integracin dentro de la UI. Descripcin del
          producto: My Life es un juego IDLE, en el que el jugador puede
          construirse su propia casa. Puede ver tambien la casa de sus amigos
          que tenga conectados a snatchap, tiene un sistema de compras,
          inventario, sistema de misiones diarias, tutoriales y un sistema grid
          que guarda el posicionamiento de los objetos construidos.
          <br />
        </>
      ),
      videoUrl: VideoMyLife,
    },
  ],
  Unity: [
    {
      titulo: "Interview Dualist",
      descripcion: (
        <>
          Interview Duelist es un juego tipo blackjack creado para la
          Game Jam 2021 de Game Jam Academy, centrado en la empleabilidad. En
          este juego de cartas, los jugadores encarnan a Roboto, un robot que
          busca su primer empleo en una prestigiosa empresa de videojuegos. Para
          lograrlo, debe impresionar al Boss en una entrevista, destacando en
          reas clave como Experience, Tech Skills, Soft Skills, y Portfolio.
          Cumple las expectativas del Boss y supera los obstculos de la
          entrevista para ayudar a Roboto a alcanzar su meta. Puedes acceder
          en{" "}
          <a
            href="https://kikritz.itch.io/interview-duelist"
            target="_blank"
            rel="noopener noreferrer"
            className="text-blue-400 hover:text-blue-600 font-semibold underline"
          >
            Itchio
          </a>
          .
        </>
      ),
      videoUrl: VideoInterviewDualist,
    },
  ],
};

const Proyectos = () => {
  return (
    <div className="p-4 md:p-8 space-y-8">
      {Object.keys(proyectosData).map((tecnologia) => (
        <motion.div
          key={tecnologia}
          className={`${colors[tecnologia]} p-4 md:p-6 rounded-lg shadow-lg`}
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          {/* Ttulo de la tecnologa */}
          <h2 className="text-xl md:text-2xl font-bold text-white mb-3 md:mb-4">
            {tecnologia}
          </h2>

          {/* Contenedor de proyectos de la tecnologa */}
          <div className="space-y-4">
            {proyectosData[tecnologia].map((proyecto, index) => (
              <motion.div
                key={index}
                className="flex flex-col md:flex-row items-start md:items-center bg-gray-800 text-white rounded-lg p-4 md:p-6 shadow-md md:space-x-6"
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.3, delay: index * 0.1 }}
              >
                {/* Texto del proyecto */}
                <div className="flex-1 mb-4 md:mb-0">
                  <h3 className="text-lg md:text-xl font-semibold">
                    {proyecto.titulo}
                  </h3>
                  <p className="text-gray-400 text-sm md:text-base">
                    {proyecto.descripcion}
                  </p>
                </div>

                {/* Video del proyecto */}
                <div className="w-full md:w-1/3 mt-4 md:mt-0">
                  <video
                    src={proyecto.videoUrl}
                    controls
                    className="rounded-lg w-full h-64 object-contain"
                  />
                </div>
              </motion.div>
            ))}
          </div>
        </motion.div>
      ))}
    </div>
  );
};

export default Proyectos;
