import React from "react";
import { motion } from "framer-motion";

const Contacto = () => {
  return (
    <div className="flex flex-col justify-center items-center min-h-screen bg-gray-100 p-4 space-y-6">
      {/* Portada */}
      <motion.div
        className="bg-gray-800 text-white p-12 rounded-lg text-center w-full max-w-lg"
        initial={{ opacity: 0, y: -30 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
      >
        <h2 className="text-3xl font-bold mb-2">Contacto</h2>
        <p className="text-gray-300">
          Gracias por visitar mi portafolio! Si te interesa saber ms sobre mi
          trabajo o tienes alguna consulta, puedes contactarme a travs de mi
          perfil de LinkedIn.
        </p>
      </motion.div>

      {/* Enlace a LinkedIn */}
      <motion.div
        className="text-center"
        initial={{ opacity: 0, y: -50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <a
          href="https://www.linkedin.com/in/iksvaku/"
          target="_blank"
          rel="noopener noreferrer"
          className="inline-block bg-blue-500 text-white py-2 px-4 rounded-md font-semibold hover:bg-blue-600 transition duration-200"
        >
          Visita mi LinkedIn
        </a>
      </motion.div>
    </div>
  );
};

export default Contacto;
