import React, { useState } from "react";
import { motion } from "framer-motion";
import { Link } from "react-router-dom";
const NotFound = () => {
 return (
   <div className="flex items-center justify-center min-h-screen bg-gray-900 text-white p-4">
     <motion.div
       initial={{ opacity: 0, y: -20 }}
       animate={{ opacity: 1, y: 0 }}
       transition={{ duration: 0.6 }}
       className="text-center space-y-6"
     >
       <motion.h1
         className="text-5xl md:text-6xl font-bold text-red-500"
         initial={{ scale: 0.8 }}
         animate={{ scale: 1 }}
         transition={{ duration: 0.4, ease: "easeOut" }}
       >
         404
       </motion.h1>
       <motion.p
         className="text-xl md:text-2xl text-gray-400"
         initial={{ opacity: 0 }}
         animate={{ opacity: 1 }}
         transition={{ delay: 0.2 }}
       >
         Oops! La pgina que buscas no existe.
       </motion.p>
       <motion.p
         className="text-md md:text-lg text-gray-500"
         initial={{ opacity: 0 }}
         animate={{ opacity: 1 }}
         transition={{ delay: 0.4 }}
       >
         Pero no te preocupes, puedes volver a la pgina principal y explorar
         mis proyectos.
       </motion.p>
       <Link to="/Home">
         <motion.button
           whileHover={{ scale: 1.05 }}
           whileTap={{ scale: 0.95 }}
           className="mt-6 px-6 py-3 bg-blue-500 hover:bg-blue-600 text-white font-semibold rounded-lg shadow-lg transition-all"
         >
           Volver al inicio
         </motion.button>
       </Link>
     </motion.div>
   </div>
 );
}
export default NotFound;